/* Navbar container */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  border-bottom: 1px solid #dee2e6;
  border-radius: 8px;
  margin: 0 2rem;
  /* margin-top: 1rem; */
  background: linear-gradient(112.1deg, rgb(32, 38, 57) 11.4%, rgb(12, 15, 23) 70.2%);
}
.text-group-notification{
  color: black;
}

.logo {
  cursor: pointer;
  font-size: 24px;
  font-weight: bold;
  color: white;
}

.no-notifications{
  color: black;
}

.nav-links {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}

.nav-links li {
  margin-right: 20px;
}

.nav-links a {
  text-decoration: none;
  color: #007bff;
  font-size: 16px;
  border-radius: 4px;
}

.nav-links a:hover {
  color: #0056b3;
  background-color: #e9ecef;
}

.auth-links {
  display: flex;
  align-items: center;
}

.user-name {
  cursor: pointer;
  margin-right: 20px;
  font-size: 16px;
  color: white;
}

.logout-button {
  background-color: #dc3545;
  color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
}

.logout-button:hover {
  background-color: #c82333;
}

.login-link {
  text-decoration: none;
  color: #007bff;
  font-size: 16px;
  border-radius: 4px;
}

.login-link:hover {
  color: #e00505;
}

/* Notification icon */
.notification-icon-container {
  position: relative;
  margin-right: 20px;
  cursor: pointer;
  border-radius: 50%;
}

.notification-icon {
  font-size: 20px;
  color: white;
}

.notification-count {
  position: absolute;
  top: -5px;
  right: -10px;
  background-color: #dc3545;
  color: white;
  border-radius: 50%;
  padding: 3px 7px;
  font-size: 8px;
}

.notification-list {
  position: absolute;
  top: 30px;
  right: 0;
  background-color: white;
  border: 1px solid #dee2e6;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  list-style: none;
  padding: 0;
  margin: 0;
  width: 250px;
  z-index: 1000;
  padding: 1rem;
}

.notification-list li {
  padding: 10px;
  border-bottom: 1px solid #dee2e6;
  border-radius: 4px;
}

.notification-list li:last-child {
  border-bottom: none;
}

.notification-list p {
  margin: 0;
  font-size: 14px;
}

.request-actions {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

.request-actions button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
}

.request-actions button:hover {
  background-color: #0056b3;
}

/* Responsive styles */
@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    padding: 10px;
  }

  .nav-links {
    flex-direction: column;
    align-items: center;
    margin-top: 10px;
  }

  .nav-links li {
    margin: 10px 0;
  }

  .auth-links {
    flex-direction: column;
    margin-top: 10px;
  }

  .notification-icon-container,
  .user-name,
  .logout-button,
  .login-link {
    margin-bottom: 10px;
  }

  .notification-list {
    width: 200px;
  }
}

@media (max-width: 480px) {
  .navbar {
    margin: 0;
    padding: 10px;
  }

  .logo {
    font-size: 20px;
  }

  .nav-links a {
    font-size: 14px;
  }

  .auth-links,
  .notification-icon-container,
  .user-name,
  .logout-button,
  .login-link {
    font-size: 14px;
  }

  .notification-list {
    width: 180px;
  }
}
