/* ChatContainer.css */

.chat-app-container {
  display: flex;
  height: 100vh;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; /* Choose a modern font */
  color: #333; 
}

/* Sidebar Styles */
.sidebar {
  width: 280px;
  background-color: #202c33;
  color: white;
  padding: 20px;
  border-right: 1px solid #343a40; /* Subtle separator */
  position: relative; /* For positioning notifications (optional) */
}

.search-bar {
  display: flex;
  align-items: center;
  background-color: #2b5278;
  border-radius: 20px; 
  padding: 12px 18px; 
  margin-bottom: 20px;
  padding-left: 40px;
}


.search-bar input::placeholder { /* Style the placeholder text */
  color: #ccc;
}

.add-button {
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  font-size: 1.2em;
  margin-left: 8px;
}

/* Chat List Styles */
.chat-list {
  overflow-y: auto; /* Make the chat list scrollable if needed */
}

.chat-item {
  display: flex;
  align-items: center;
  padding: 12px 15px;
  cursor: pointer;
  border-bottom: 1px solid #343a40;
  transition: background-color 0.2s; /* Smooth transition for hover effect */
}

.chat-item:hover {
  background-color: #2b5278; 
}

.chat-item img {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  margin-right: 12px;
}
.member-photo{
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
}
.chat-item-info h4 {
  margin: 0 0 3px 0;
  font-size: 1em;
  font-weight: 600; /* Slightly bolder font weight */
}

.chat-item-info p {
  margin: 0;
  font-size: 0.9em;
  color: #888;
  white-space: nowrap; /* Prevent text from wrapping */
  overflow: hidden;
  text-overflow: ellipsis; /* Add ellipsis if text overflows */
}

/* Optional: Notification Badge on Chat Item */
.chat-item .notification-badge {
  background-color: #f00; /* Red notification badge */
  color: white;
  font-size: 0.8em;
  padding: 2px 6px;
  border-radius: 50%;
  position: absolute;
  top: 10px;
  right: 10px;
}

/* Main Chat Area Styles */
.main-chat-area {
  flex: 1;
  display: flex;
  flex-direction: column;
  background-color: #202c33;
  color: white;
}

.chat-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  background-color: #2b5278; /* Match header background to search bar */
  border-bottom: 1px solid #343a40;
}

.chat-header-left {
  display: flex;
  align-items: center;
}

.chat-header-left img {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  margin-right: 12px;
}

.chat-header-left h3 {
  margin: 0;
  font-size: 1.3em;
  font-weight: 500;
}

.chat-header-right {
  display: flex;
}

.action-button {
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  margin-left: 15px;
  font-size: 1.2em;
  transition: color 0.2s; /* Smooth color transition on hover */
}

.action-button:hover {
  color: #a9a9a9; /* Lighter color on hover */
}

.chat-body {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
}

.chat-footer {
  padding: 10px 20px;
  border-top: 1px solid #343a40;
}

.message-input-area {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #2b5278;
  border-radius: 20px; /* More rounded corners */
  padding: 4px 12px; 
}

.message-input-area input {
  margin-top: 1rem;
  flex: 1;
  background: transparent;
  height: 100%;
  border: none;
  color: white;
  padding: 10px;
  font-size: 1em;
}
.message-input-area input:focus {
  flex: 1;
  background: transparent;
  height: 100%;
  border: none;
  color: white;
  padding: 10px;
  font-size: 1em;
  outline: none; /* Remove the default outline on active state */
}

.message-input-area input::placeholder { 
  color: #ccc;
}

.send-button {
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  font-size: 1.2em;
  margin-left: 8px;
}

/* Group Details Panel */
.group-details-panel {
  position: absolute; 
  top: 0;
  right: 0;
  width: 300px;
  height: 100vh;
  background-color: #202c33;
  color: white;
  padding: 20px;
  box-shadow: -5px 0 10px rgba(0, 0, 0, 0.2); 
  z-index: 10;
  overflow-y: auto; /* Allow scrolling in the details panel */
}

.group-details-panel h2 {
  margin-bottom: 15px;
  font-size: 1.5em;
}

.group-details-panel p {
  margin-bottom: 10px;
}

.shared-media, .shared-files {
  margin-top: 20px;
}

.shared-media h3, .shared-files h3 {
  font-size: 1.1em;
  margin-bottom: 10px; 
}

.logout-button, 
.block-user-button {
  display: block;
  width: calc(100% - 20px); /* Account for padding */
  padding: 10px;
  margin: 20px auto 0; /* Center buttons with margin: auto */
  background-color: #d9534f;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9em;
  text-align: center; /* Center text */
}


/* ... previous styles ... */

/* Group Details Panel - Members List Styles */
.group-details-panel .members-list {
  margin-top: 20px;
}

.group-details-panel .members-list ul {
  list-style: none;
  padding: 0;
}
.member-item{
  margin: 2rem;
  /* border: 2px solid rebeccapurple; */
  display: flex;
  gap: 1rem;
  justify-content: center;
  align-items: center;
}

.group-details-panel .member-item {
  display: flex;
  align-items: center;
  margin-bottom: 8px; 
}

.group-details-panel .member-item img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-right: 10px;
}

.group-details-panel .member-item.admin span {
  font-weight: 600; /* Make admin name bold */
}

 .remove-member-button {
  padding: 0.8rem;
  border-radius: 10%;
  background-color: rgb(70, 70, 97);
  border: none;
  color: #cd1812; /* Red color for remove button */
  cursor: pointer;
  margin-left: auto; /* Push to the right */
  font-size: 0.8em;
}