/* MessageContainer.css */

.message-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  overflow-y: auto;
  padding: 20px;
}

.date-separator {
  text-align: center;
  margin: 15px 0;
  color: #888;
  font-size: 0.9em;
}

.message {
  display: flex;
  align-items: flex-start; 
  margin-bottom: 10px;
}

/* Sent Messages */
.sent {
  justify-content: flex-end;
}



.sent .message-bubble {
  padding: 2rem;
  background-color: #2b5278; 
  color: white;
  margin-left: 10px;
  border-top-right-radius: 5px; /* Angled corner */
}

/* Received Messages */
.received {
  justify-content: flex-start;
}

.received .message-bubble {
  margin-left: 1rem;
  padding: 2rem;
  background-color: #1e1f26; 
  color: rgb(255, 255, 255);
  margin-right: 10px;
  border-top-left-radius: 5px; /* Angled corner */
}

.message-bubble {
  padding: 12px 18px; /* Increased padding */
  border-radius: 18px; 
  max-width: 70%;
  word-break: break-word;
  position: relative;
  box-shadow: 0px 1px 2px rgba(255, 255, 255, 0.1); /* Subtle shadow */
}

.profile-pic {
  width: 35px; 
  height: 35px;
  border-radius: 50%;
}

.timestamp {
  font-size: 0.8em;
  color: #ffffff;
  position: absolute;
  bottom: 5px;
  right: 10px;
}

.no-messages {
  text-align: center;
  margin-top: 20px;
  color: #888;
}